const BASE_URL = 'api/blog'

export function getBlog($http, query) {
    return $http.get(BASE_URL, { params: query })
}

export function addBlog($http, query) {
    return $http.post(`${BASE_URL}/add`, query)
}

export function starBlog($http, query) {
    return $http.get(`${BASE_URL}/star`, { params: query })
}
